.patient-selection-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.patient-selection-modal {
  background: var(--background-color);
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  max-height: 80vh;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 10px;
}

.modal-header h2 {
  margin: 0;
  color: var(--text-color);
}

.close-button {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: var(--text-color);
}

.modal-content {
  display: flex;
  flex-direction: column;
  gap: 15px;
  flex-grow: 1;
  overflow: hidden;
}

.patient-search-input {
  width: 100%;
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background: var(--input-background-color);
  color: var(--text-color);
}

.patient-list-container {
  flex-grow: 1;
  overflow-y: auto;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 10px;
  background: var(--secondary-background-color);
}

.patient-item {
  padding: 10px;
  border-bottom: 1px solid var(--border-color);
  cursor: pointer;
  color: var(--text-color);
}

.patient-item:last-child {
  border-bottom: none;
}

.patient-item:hover {
  background: var(--hover-background-color);
}

.patient-item.no-patients {
  text-align: center;
  color: var(--text-color-light);
}

.create-patient-btn {
  background-color: var(--primary-button-background);
  color: var(--primary-button-text);
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
}

.create-patient-btn:hover {
  background-color: var(--primary-button-hover-background);
}

.create-patient-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
  padding: 15px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  background: var(--secondary-background-color);
}

.create-patient-form h3 {
  margin-top: 0;
  color: var(--text-color);
}

.create-patient-form input,
.create-patient-form select {
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background: var(--input-background-color);
  color: var(--text-color);
}

.create-patient-form select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23000000%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095.1c3.6-3.6%205.4-7.8%205.4-12.8%200-5-1.8-9.3-5.4-12.9z%22%2F%3E%3C%2Fsvg%3E');
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 12px;
  padding-right: 30px;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.form-actions button {
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.form-actions button:first-child {
  background-color: var(--primary-button-background);
  color: var(--primary-button-text);
  border: none;
}

.form-actions button:first-child:hover {
  background-color: var(--primary-button-hover-background);
}

.form-actions button:last-child {
  background-color: var(--secondary-button-background);
  color: var(--secondary-button-text);
  border: 1px solid var(--secondary-button-border);
}

.form-actions button:last-child:hover {
  background-color: var(--secondary-button-hover-background);
}
